}

@media screen and (max-width: 783px) {
  .mr-auto {
    display: inline-block;
  }
}

@media screen and (max-width: 477px) {
  .logoSite {
    display: contents;
  }
  .titleSite {
    display: none;
  }
  #basic-navbar-nav {
    text-align: center;
  }
}


